.container {
  max-width: 300px;
}

.item {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 60px;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(90, 90, 90);
}

.item:hover {
  background-color: rgb(235, 235, 235);
}

.item  .clear{
  display: none;
}

.item:hover .clear{
  display: inline-block;
  position: absolute;
  right: 0px;
  height: 60px;
  line-height: 60px;
  background-color: #ccc;
}

.item:hover .clear:hover {
  background-color: #aaa;
}

.item:hover .clear img {  margin: 0 5px 0 5px
}

.item.active {
  background-color: rgb(235, 235, 235);
}

.item .avatar-wrapper {
  padding-left: 10px;
  padding-right: 10px;
  height: 40px;
}

.item .name {
  font-size: 18px;
}

.item .avatar-wrapper img {
  max-height: 40px;
  max-width: 40px;
  border-radius: 50%;
}





